.button {
  width: 4.5rem;
  height: 4.5rem;
  background-color: var(--color-button);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    height: 0.3rem;
    width: 68%;
    background-color: white;
    border-radius: 5rem;
    top: 50%;
    left: 49.9%;
    transform: translate(-50%, -50%);
  }

  &::after {
    content: '';
    position: absolute;
    width: 0.3rem;
    height: 68%;
    background-color: white;
    border-radius: 5rem;
    top: 50%;
    left: 49.8%;
    transform: translate(-50%, -50%);
  }
}
